import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'date-input',
  styleUrl: 'date-input.css',
  shadow: true,
})
export class DateInput {
  /**
   * The name of the input field.
   */
  @Prop() name!: string;

  /**
   * The label for the input field.
   */
  @Prop() label!: string;

  /**
   * The current value of the date input.
   */
  @Prop({ mutable: true, reflect: true }) value: string = '';

  /**
   * The minimum allowed date (YYYY-MM-DD).
   */
  @Prop() min?: string;

  /**
   * The maximum allowed date (YYYY-MM-DD).
   */
  @Prop() max?: string;

  /**
   * Whether the input is disabled.
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the input value changes.
   */
  @Event() myChange: EventEmitter<string> | undefined;

  private handleChange = (event: Event) => {
    if (this.disabled) {
      return;
    }

    const input = event.target as HTMLInputElement;
    this.value = input.value;
    this.myChange?.emit(this.value);
  };

  render() {
    return (
      <div class={{ 'date-wrapper': true, 'date-wrapper--disabled': this.disabled }}>
        <label htmlFor={this.name} class="date-label">{this.label}</label>
        <input
          type="date"
          id={this.name}
          name={this.name}
          value={this.value}
          min={this.min}
          max={this.max}
          disabled={this.disabled}
          onChange={this.handleChange}
          class="date-field"
          placeholder="YYYY-MM-DD"
        />
      </div>
    );
  }
}
